









.mint-actionsheet {
  position: fixed;
  background: #e0e0e0;
  width: 100%;
  text-align: center;
  bottom: 0;
  left: 50%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.mint-actionsheet-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.mint-actionsheet-listitem {
  border-bottom: solid 1px #e0e0e0;
}

.mint-actionsheet-listitem, .mint-actionsheet-button {
  display: block;
  width: 100%;
  height: 45px;
  line-height: 45px;
  font-size: 18px;
  color: #333;
  background-color: #fff;
}

.mint-actionsheet-listitem:active, .mint-actionsheet-button:active {
  background-color: #f0f0f0;
}

.actionsheet-float-transition {
  -webkit-transition: -webkit-transform .3s ease-out .1s;
  transition: -webkit-transform .3s ease-out .1s;
  transition: transform .3s ease-out .1s;
  transition: transform .3s ease-out .1s, -webkit-transform .3s ease-out .1s;
}

.actionsheet-float-enter, .actionsheet-float-leave {
  -webkit-transform: translate3d(-50%, 100%, 0);
          transform: translate3d(-50%, 100%, 0);
}



