











.page-toast {}
.page-toast-wrapper {
    padding: 0 20px;
    top: 50%;
    position: absolute;
    width: 100%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.page-toast-wrapper button:not(:last-child) {
    margin-bottom: 20px;
}



