}

.mint-radio {}

.mint-radio.is-right {

    float: right;
}

.mint-radio-label {

    vertical-align: middle;

    margin-left: 6px;
}

.mint-radio-core {

    -webkit-appearance: none;

       -moz-appearance: none;

            appearance: none;

    outline: 0;

    background-color: #fff;

    border-radius: 100%;

    border: 1px solid #ccc;

    position: relative;

    width: 20px;

    height: 20px;

    vertical-align: middle;
}

.mint-radio-core::after {

    content: " ";

    border-radius: 100%;

    top: 5px;

    left: 5px;

    position: absolute;

    width: 8px;

    height: 8px;

    -webkit-transition: -webkit-transform .2s;

    transition: -webkit-transform .2s;

    transition: transform .2s;

    transition: transform .2s, -webkit-transform .2s;

    -webkit-transform: scale(0);

            transform: scale(0);
}

.mint-radio-core:checked {

    background-color: #ffc900;

    border-color: #ffc900;
}

.mint-radio-core:checked::after {

    background-color: #fff;

    -webkit-transform: scale(1);

            transform: scale(1);
}

.mint-radio-core[disabled] {

    background-color: #d9d9d9;

    border-color: #ccc;
}



